.tag {
    height: 30px !important;
    margin: -5px auto 5px auto;
    background-color: orange;
    width: 140px;
    height: auto;
    line-height: 30px !important;
    border-radius: 5px;
    -webkit-box-shadow: 0 0 5px #ccc;
            box-shadow: 0 0 5px #ccc;
    display: table;
}

.tag p {
    display: table-cell;
    vertical-align: middle;
}



