.main-wrapper {
    width:100%;
    margin: 0;
    padding: 0;
    max-width: 100%;
}

.modal-contact {
    z-index: 990;
}

.btn-close-contact {
    cursor: pointer;
    background-color: #4775de;
    padding: 15px;
    border-radius: 5px;
    color: white !important;
    font-family: 'Lato';
    -webkit-transition: background-color 0.2s;
    -o-transition: background-color 0.2s;
    transition: background-color 0.2s;
    position: relative;
    margin: 0 auto;
}

.btn-close-contact:hover {
    text-decoration: none !important;
    background-color: #608aec;
}

