.project-tile,
.project-tile-mobile,
.project-tile-mobile-single{
    height: 400px;
    margin: 10px auto 80px auto;
    width: 1000px;
    -webkit-transition: -webkit-transform 0.2s;
    transition: -webkit-transform 0.2s;
    -o-transition: transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
    border-radius: 15px;
    color: black;
    -webkit-box-shadow: 0 0 40px #ccc;
            box-shadow: 0 0 40px #ccc;
}

.project-tile:hover,
.project-tile-mobile:hover,
.project-tile-mobile-single:hover {
    text-decoration: none !important;
    color: black;
    -webkit-transform: scale(1.02);
        -ms-transform: scale(1.02);
            transform: scale(1.02);
}

.project-tile h2,
.project-tile-mobile h2,
.project-tile-mobile-single h2 {
    margin: 120px auto 0 auto;
    text-align: left;
}

.project-tile-mobile,
.project-tile-mobile-single {
    display: none;
}

.project-preview-tag {
    background-color: orange;
    width: 140px;
    line-height: 30px;
    border-radius: 5px;
    -webkit-box-shadow: 0 0 5px #ccc;
            box-shadow: 0 0 5px #ccc;
    padding: 0 !important;
}

.img-cont {
    padding: 0 !important;
}

@media (max-width: 1200px) {
    .project-tile,
    .project-tile-mobile,
    .project-tile-mobile-single {
        height: 320px !important;
        width: 800px !important;
        margin: 10px auto 60px auto;
    }

    .project-tile h2,
    .project-tile-mobile h2,
    .project-tile-mobile-single h2 {
        margin-top: 90px;
    }

    .text-cont h2 {
        font-size: 35px !important;
        letter-spacing: -2px !important;
    }
}

@media (max-width: 1000px) {
    .project-tile,
    .project-tile-mobile,
    .project-tile-mobile-single {
        height: 240px !important;
        width: 600px !important;
        margin: 10px auto 60px auto;
    }

    .project-tile h2,
    .project-tile-mobile h2,
    .project-tile-mobile-single h2 {
        margin-top: 55px;
        font-size: 40px !important;
    }

    .project-tile p,
    .project-tile-mobile p,
    .project-tile-mobile-single p {
        font-size: 15px;
    }

    .text-cont h2 {
        font-size: 30px !important;
    }
}

@media (max-width: 767px) {
    .project-tile-mobile {
        height: auto !important;
        width: 600px !important;
        margin: 20px auto 70px auto;
        display: block;
    }

    .project-tile-mobile-single {
        height: 240px !important;
        width: 600px !important;
        margin: 20px auto 70px auto;
        display: block;
    }

    .project-tile {
        display: none;
    }

    .project-tile-mobile,
    .project-tile-mobile-single {
        width: 90% !important;
    }

    .text-cont h2 {
        font-size: 30px;
    }

    .project-tile-mobile:hover,
    .project-tile-mobile-single:hover {
        -webkit-transform: none;
            -ms-transform: none;
                transform: none;
    }
}



